<template>
  <div>
  <center>
  <h1>Top herb estimated prices</h1>
  <div class="herbcontainer">
  <div v-for="(row, index) in rows" :key='index'>
    <div class="herbblock" :style="backColorStyleOf(index)" v-on:click="detail(row)">
      <div class="herbrank" :style="backColorStyleOf(index+2)">{{index+1}}</div>
      <img src="@/assets/png/herb1.png" width=50px style="vertical-align:top">
      <div class="herbid">Herb#{{row.SerialId}}</div>
      <div class="herbprop">Planted at: {{timeAgo(row.CreateAt)}}</div>
      <div class="herbprop">Estimated price: {{priceOf(row.CreateAt)}}</div>
    </div>
   </div>
  </div>
  </center>
  </div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf, backColorStyleOf } from '../util/funcs.js';


export default {
  name: 'Home',
  beforeMount() {
    const currentUser = this.$Amplify.Auth.currentUserInfo();
    console.log('call herbListHerbs');
    API.post('herbListHerbs', '', {}).then(response => {
      console.log('herbListHerbs return : %o',  response);
      this.rows = response.Herbs;
    }).catch(error => {
      console.log(error);
    });

  },

  data () {
    return {
      rows: [ ]
    }
  },

  methods: {
    timeAgo(date) {
      return timeAgo(date);
    },
    priceOf(createAt) {
      return priceOf(createAt);
    },
    backColorStyleOf(index) {
      return backColorStyleOf(index);
    },
    detail(row) {
      alert(row);
    }
  }
}
</script>



