<template>
  <div>
  <center>
  <div class="herbheader">
  <p><img src="@/assets/png/herb3.png" height="80px" align="right"></p>
  <p style="font-weight:bold;">First Blockchain Collectible Game With Real Herbs</p>
  <p>Every single herb you own in the game 100% belongs to you and you can trade or transfer it to anyone anytime you like.</p>
  </div>
  <div class="herbcontainer">
  <div v-for="(row, index) in rows" :key='index'>
    <div class="herbblock" :style="backColorStyleOf(index)" v-on:click="buy(row)">
      <img src="@/assets/png/herb1.png" width=50px style="vertical-align:top">
      <div class="herbid">Herb#{{row.SerialId}}</div>
      <div class="herbtitle">Ask price: {{ethOfWei(row.Ask)}}</div>
      <div class="herbprop">Planted at: {{timeAgo(row.CreateAt)}}</div>
      <div class="herbprop">Estimated price: {{priceOf(row.CreateAt)}}</div>
    </div>
   </div>
  </div>
  </center>
  </div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf, backColorStyleOf, ethOfWei } from '../util/funcs.js';

export default {
  name: 'Auction',

  data () {
    return {
      rows: [ ]
    }
  },

  async beforeMount() {
    const currentUser = await this.$Amplify.Auth.currentUserInfo();
    console.log('call herbListHerbs');
    API.post('herbListAsks', '', {}).then(response => {
      console.log('herbListAsks return : %o',  response.Asks);
      this.rows = response.Asks;
    }).catch(error => {
      console.log(error);
    });
  },


  methods: {
    async buy(ask) {
      const currentUser = await this.$Amplify.Auth.currentUserInfo();
      console.log("!!!!!!!!!!!!!!!!!!!!!")
      console.log(currentUser);
      if (currentUser == null) {
        this.$router.push({path: '/auth' });
        return;
      }

      console.log(currentUser.id);
      console.log(ask.AccId);

      if (currentUser.id == ask.AccId) {
        alert("yours herb");
      } else {
        this.$router.push({path: '/buy', query:{'serialId': ask.SerialId, 'herbId': ask.HerbId, 'askPrice': ask.Ask, 'askAccId': ask.AccId , 'askAccName': ask.AccName, 'createAt': ask.CreateAt}});
     }
    },

    timeAgo(date) {
      return timeAgo(date);
    },
    priceOf(createAt) {
      return priceOf(createAt);
    },
    backColorStyleOf(index) {
      return backColorStyleOf(index);
    },
    ethOfWei(wei) {
      return ethOfWei(wei);
    }
  }
}
</script>



