<template>
  <table><tbody>
    <tr v-for="row in rows">
      <td class="herbid">
         {{row.Id}}
      </td>
      <td class="price">
        {{row.Price}}
      </td>
      <td class="time">
        {{timeAgo(row.PlantAt)}}
      </td>
    </tr>
  </tbody></table>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth } from 'aws-amplify';
import { timeAgo } from '../util/funcs.js';

export default {
  name: 'Home',

  data () {
    return {
      rows: [ ]
    }
  },

  async created() {

    this.rows = [ {'Id': '1', 'Price': '$1', 'PlantAt': '1552103600000'} ]
  },

  methods: {
    timeAgo(date) {
      return timeAgo(date);
    }
  }
}
</script>



