<template>
  <div class="container shifted"><center>
    <h2 class="h2">
      Sell your herb#{{serialId}} now 
    </h2>
    <table>
      <tr><td>Price: <input v-model="herbPrice" placeholder="Herb price"></td></tr>
      <tr><td colspan="2" align="center"><button class="block" v-on:click="sell">Sell now</button></td></tr>
    </table>
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf, weiOfEth } from '../util/funcs.js';

const uuidv4 = require('uuid/v4');

export default {
  name: 'Sell',

  data () {
    return {
      serialId: null,
      herbId: null,
      herbPrice: 0 
    }
  },

  async created() {
    this.serialId = this.$route.query.serialId;
    this.herbId = this.$route.query.herbId;
    this.herbPrice = this.$route.query.refPrice;
  },

  methods: {
    async sell() {
      const currentUser = await this.$Amplify.Auth.currentUserInfo();

      API.post('herbNewAsk', '', {body: {'AskId': uuidv4(), 'SerialId': this.serialId, 'HerbId': this.herbId, 'Ask': weiOfEth(this.herbPrice), 'AccName':currentUser.username}}).then(response => {
        this.$router.push('/Auction');
      }).catch(error => {
        alert('something went wrong');
        this.buildTapped = false;
        console.log(error)
      });
    },
    weiOfEth(eth) {
      return weiOfEth(eth);
    }
  }
}
</script>



