<template>
  <div class="container"><center>
    <p style="margin-left:5%; font-weight:bold; float:left;">Herb #{{this.serialId}}</p>
    <p style="margin-left:5%;"> <table width="100%"><tr>
    <td width="50px"><img src="@/assets/png/calendar.png" width="30px"></td>
    <td width="150px"><div class="">Planted at</div><div>{{timeAgo(this.createAt)}}</div></td>
    <td><div>Owned by</div><div>{{this.askAccName}}</div></td>
    </tr></table> </p>
    <p style="margin-left:5%;"> <table width=100%><tr>
    <td width="150px">Buy now price<br>{{ethOfWei(this.herbPrice)}}</td>
    <td align=right><div class="herbbutton" v-on:click="buy">Buy</div> </td>
    <td width="20px"></td>
    </tr></table> </p>

    <div style="margin:20px">
      <line-chart :chart-data="chartdata" :options="options"> </line-chart>
    </div>
    
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf, ethOfWei } from '../util/funcs.js';
import VueCharts from 'vue-chartjs'
import { Bar, Line } from 'vue-chartjs'
import LineChart from '../util/LineChart.js'

Vue.component('line-chart', LineChart)


const uuidv4 = require('uuid/v4');

export default {
  name: 'Buy',
  components : { LineChart },

  data () {
    return {
      serialId: null,
      herbId: null,
      herbPrice: 0,
      askAccId: null,
      askAccName: null,
      createAt: 0,

      chartdata: { },
      options: {responsive: true, maintainAspectRatio: false }
    }
  },

  async created() {
    this.serialId = this.$route.query.serialId;
    this.herbId = this.$route.query.herbId;
    this.herbPrice = this.$route.query.askPrice;
    this.askAccId = this.$route.query.askAccId;
    this.askAccName = this.$route.query.askAccName;
    this.createAt = this.$route.query.createAt;

    this.chartdata = {
        labels: ['Initial', 'Ask', 'One year later'],
        datasets:[
          {
            borderColor: '#FC2525',
            pointBorderColor: 'white',
            borderWidth: 1,
            pointBorderColor: 'white',
            data: [1, this.priceOf(this.createAt), this.oneYearLaterPriceOf(this.createAt)]
          }
        ]
      };
  },

  methods: {
    async buy() {
      const currentUser = await this.$Amplify.Auth.currentUserInfo();
      console.log("!!!!!!!!!!!!!!!!!!!!!!");
      console.log(currentUser.id);
      console.log(this.askAccId);
      if (currentUser.id == this.askAccId) {
        alert('You can not buy your plant');
        return;
      }

      API.post('herbNewBid', '', {body: {'BidId': uuidv4(), 'SerialId': this.serialId, 'HerbId': this.herbId, 'Bid': this.herbPrice, 'BAccName':currentUser.username}  }).then(response => {
        this.$router.push('/MyPlants');
      }).catch(error => {
        alert('something went wrong');
        console.log(error)
      });
    },

    timeAgo(date) {
      return timeAgo(date);
    },
    priceOf(createAt) {
      return priceOf(createAt);
    },
    oneYearLaterPriceOf(createAt) {
      return priceOf(createAt-365*24*3600*1000);
    },
    backColorStyleOf(index) {
      return backColorStyleOf(index);
    },
    ethOfWei(wei) {
      return ethOfWei(wei);
    }
  }
}
</script>



