<template>
  <div class="container shifted"><center>
    <div v-show="rows.length != 0">
      <h1 class="h1">
        My planted herbs
      </h1>
      <div class="herbcontainer">
        <div v-for="(row, index) in rows" :key='index'>
          <div class="herbblock" :style="backColorStyleOf(index)" v-on:click="sell(row)">
            <img src="@/assets/png/herb1.png" width=50px style="vertical-align:top">
            <div class="herbid">Herb#{{row.SerialId}}</div>
            <div class="herbprop">Planted at: {{timeAgo(row.CreateAt)}}</div>
            <div class="herbprop">Estimated price: {{priceOf(row.CreateAt)}}</div>
          </div>
        </div>
      </div>
    </div>
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf, backColorStyleOf } from '../util/funcs.js';

const uuidv4 = require('uuid/v4');

export default {
  name: 'MyPlants',

  data () {
    return {
      rows: [],
      herbCount: 1
    }
  },

  async beforeMount() {
    const currentUser = await this.$Amplify.Auth.currentUserInfo();
    console.log('call herbMyHerbs');
    API.post('herbMyHerbs', '', {}).then(response => {
      console.log('herbMyHerbs return : %o',  response.Herbs);
      this.rows = response.Herbs;
      console.log('rows.length: ' + this.rows.length);
    }).catch(error => {
      console.log(error);
    });
  },

  methods: {
    async sell(row) {
        this.$router.push({path: '/sell', query:{'serialId': row.SerialId, 'herbId': row.HerbId, 'refPrice': priceOf(row.CreateAt) }});
    },

    timeAgo(date) {
      return timeAgo(date);
    },
    priceOf(createAt) {
      return priceOf(createAt);
    },
    backColorStyleOf(index) {
      return backColorStyleOf(index);
    }
  }
}
</script>



