<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      Plant a new herb now
    </h1>
    <img src="@/assets/png/herb1.png" width=50px>
    <div><button class="block button200" v-on:click="plant">Plant now</button></div>
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf } from '../util/funcs.js';

const uuidv4 = require('uuid/v4');

export default {
  name: 'Plant',

  methods: {
    async plant() {
      const currentUser = await this.$Amplify.Auth.currentUserInfo();

      API.post('herbNewHerb', '', {body: {'HerbId': uuidv4(), 'AccName':currentUser.username}  }).then(response => {
        this.$router.push('/');
      }).catch(error => {
        alert('something went wrong');
        this.buildTapped = false;
        console.log(error)
      });
    },

    timeAgo(date) {
      return timeAgo(date);
    },
    priceOf(createAt) {
      return priceOf(createAt);
    }
  }
}
</script>



