export function timeAgo(date) {
  var diff = Math.round((new Date() - date) / 1000)
  const times = [["second", 1], ["minute", 60], ["hour", 3600], ["day", 86400], ["week", 604800], ["month", 2592000], ["year", 31536000]]
  for (var t = 0; t < times.length; t++) {
    if (diff < times[t][1]) {
      if (t == 0) {
        return "Just now"
      } else {
        diff = Math.round(diff / times[t - 1][1])
        return diff + " " + times[t - 1][0] + (diff == 1?" ago":"s ago")
      }
    }
  }
}

export function priceOf(createAt) {
  var days = (Date.now()/1000 - createAt/1000)/24/3600;

  return parseFloat(Math.pow(1.002, days)).toFixed(4);
}

export function backColorStyleOf(index) {
  let colors = ['#dbfef6', '#fee0d1', '#ffebb3', '#fed1f2', '#f0fdd2', '#b2d6fd', '#fee6d2', '#d1feff', '#c4b2fd'];
  return {'background-color': colors[index%10]};
}

export function weiOfEth(eth) {
  return (eth * Math.pow(10, 18)).toFixed(0);
}

export function ethOfWei(wei) {
  return (wei / Math.pow(10, 18)).toFixed(4);
}




