
<template>
  <div class="nav">
    <table class="main" width="100%"><tr>
      <td><img src="@/assets/png/htc.png" width="50px" align="middle"></td>
      <td width="50px"><a class="item" v-on:click="leadboards">Leadboards</a></td>
      <td width="50px"><a class="item" v-on:click="auction">Auction</a></td>
      <td width="50px"><a class="item" v-on:click="profile" v-if="user">Profile</a></td>
      <td width="100px"><a class="item" v-on:click="myPlants">My Plants</a></td>
      <td width="50px"><a id="plantbutton" v-on:click="plant">Plant</a></td>
    </tr></table>
  </div>
</template>

<!--
      <a class="item" v-on:click="products">Products</a>
      <a class="item" v-on:click="referral">Referral</a>
-->

<script>
import { Auth, Hub } from 'aws-amplify';
import AmplifyStore from '../store';


export default {
  name: 'Menu',
  data () {
    return {}
  },
  computed: {
    user() { 
      return AmplifyStore.state.user
    }
  },
  methods: {
    leadboards: function() {
        this.$router.push('/')
    },
    plant: function() {
        this.$router.push('/plant')
    },
    auction: function() {
        this.$router.push('/auction')
    },
    myPlants: function() {
        this.$router.push('/myPlants')
    },
    products: function() {
        this.$router.push('/products')
    },
    referral: function() {
        this.$router.push('/referral')
    },
    profile: function() {
        this.$router.push('/profile')
    },
    signOut: function() {
        this.$router.push('/auth/signOut')
    }
  }
}
</script>




